*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  height: 5rem;
  width: 100%;
  background: rgb(255, 241, 42);
  color: #000;
  position: fixed;
  z-index: 1000;
}


.navitems {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  
}


nav img{
  height: 3rem;
  width: 19%;
  margin-left: 25px;
  margin-top: 1rem;
  border-radius: 12px;
  
}

.navitems button {
  width: 19%;
  height: 3rem;
  border: none;
  border-radius: 12px;
  background-color: #000000;
  color: white;
  margin: auto 0px;
  margin-right: 25px;
  cursor: pointer;
  font-weight: bold;
  font-size: 35px;
  margin-top: 1rem;
}


.fetchbtn:hover {
  background-color: rgb(255, 44, 44);
  border: none;
  color: black;
  cursor: pointer;
}  


.userdatacontainer {
  width: 100%;
  height: 830px;
  background: rgb(255, 44, 44) ;
  padding-bottom: 2rem;

}


#main {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
  padding-top: 2rem;
}

.avatar {
  border-radius: 50%;
}

.profile_cont {
  border-radius: 50%;
  background-color: rgb(0, 0, 0);
  margin: 5px;
  padding: 10px;
  color: white;
  width: 25%;
  text-align: center;
  margin-top: 8%;
  cursor: pointer;
}

.profile_cont:hover {
  background-color: rgb(56, 255, 99);
  color: #000;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: black;
  color: white;
  /* margin-top: 500px; */
  font-size: 1rem;
}